html {
  scroll-behavior: smooth;
}

body {
  min-width: 320px;

  font-family: Open Sans, "Arial", sans-serif;
  font-weight: 400;
  @include font-size;

  color: rgba($black, 1);
}

* {
  box-sizing: border-box;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: inherit;
}
